//    

/**
 *    
 * @returns {boolean}   
 */
export const isInAppBrowser = (): boolean => {
  const userAgent = navigator.userAgent.toLowerCase();
  
  //    
  const inAppBrowsers = [
    'kakao',           // 
    'naver',           //  
    'whale',           //   ()
    'instagram',       // 
    'facebook',        // 
    'twitter',         // 
    'line',            // 
    'wechat',          // 
    'micromessenger',  //  ()
    'webview',         //  
    'fb_iab',          //  
    'fbios',           //  iOS
    'fban',            //  
    'trill',           // 
    'bytedance',       //  
  ];
  
  return inAppBrowsers.some(browser => userAgent.includes(browser));
};

/**
 *    
 * @returns {boolean}   
 */
export const isMobileBrowser = (): boolean => {
  const userAgent = navigator.userAgent.toLowerCase();
  
  const mobileKeywords = [
    'mobile',
    'android',
    'iphone',
    'ipad',
    'ipod',
    'blackberry',
    'windows phone',
  ];
  
  return mobileKeywords.some(keyword => userAgent.includes(keyword));
};

/**
 *    
 * @returns {object}   
 */
export const getBrowserInfo = () => {
  const userAgent = navigator.userAgent.toLowerCase();
  
  return {
    isInApp: isInAppBrowser(),
    isMobile: isMobileBrowser(),
    userAgent: navigator.userAgent,
    isChrome: userAgent.includes('chrome') && !userAgent.includes('edge'),
    isSafari: userAgent.includes('safari') && !userAgent.includes('chrome'),
    isFirefox: userAgent.includes('firefox'),
    isEdge: userAgent.includes('edge'),
  };
};

/**
 * Google OAuth   
 * @returns {boolean} OAuth   
 */
export const isOAuthCompatibleBrowser = (): boolean => {
  return !isInAppBrowser();
};

/**
 *     
 * @returns {string}  
 */
export const getBrowserGuidanceMessage = (): string => {
  const browserInfo = getBrowserInfo();
  
  if (browserInfo.isInApp) {
    if (browserInfo.isMobile) {
      return '    . "Safari "  "Chrome "  .';
    } else {
      return '   . Chrome, Safari, Firefox     .';
    }
  }
  
  return '';
};
