import React, { createContext, useContext } from 'react';
import { useAuth } from './AuthContext';
import { 
  mockDiaryEntries, 
  mockContextData, 
  getDiaryByDate, 
  getContextByDate, 
  getRecentDiaries, 
  getDiariesByDateRange, 
  hasDataForDate, 
  getDiaryCountByMonth 
} from '../data/mockData';

interface DataContextType {
  isUsingMockData: boolean;
  diaryEntries: typeof mockDiaryEntries;
  contextData: typeof mockContextData;
  getDiaryByDate: typeof getDiaryByDate;
  getContextByDate: typeof getContextByDate;
  getRecentDiaries: typeof getRecentDiaries;
  getDiariesByDateRange: typeof getDiariesByDateRange;
  hasDataForDate: typeof hasDataForDate;
  getDiaryCountByMonth: typeof getDiaryCountByMonth;
}

const DataContext = createContext<DataContextType | undefined>(undefined);

export const useData = () => {
  const context = useContext(DataContext);
  if (context === undefined) {
    throw new Error('useData must be used within a DataProvider');
  }
  return context;
};

interface DataProviderProps {
  children: React.ReactNode;
}

export const DataProvider: React.FC<DataProviderProps> = ({ children }) => {
  const { isAuthenticated, isDemoMode } = useAuth();
  
  //    
  // 1.  : mockData 
  // 2.  : mockData 
  // 3.  +  : Firebase  ( )
  const isUsingMockData = isDemoMode || !isAuthenticated;

  //  mockData ,  Firebase     
  const value: DataContextType = {
    isUsingMockData,
    diaryEntries: isUsingMockData ? mockDiaryEntries : [], //  Firebase  
    contextData: isUsingMockData ? mockContextData : [], //  Firebase  
    getDiaryByDate: isUsingMockData ? getDiaryByDate : () => undefined, //  Firebase  
    getContextByDate: isUsingMockData ? getContextByDate : () => undefined,
    getRecentDiaries: isUsingMockData ? getRecentDiaries : () => [],
    getDiariesByDateRange: isUsingMockData ? getDiariesByDateRange : () => [],
    hasDataForDate: isUsingMockData ? hasDataForDate : () => false,
    getDiaryCountByMonth: isUsingMockData ? getDiaryCountByMonth : () => 0,
  };

  return (
    <DataContext.Provider value={value}>
      {children}
    </DataContext.Provider>
  );
};
